.body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

@media screen and (width >= 1024px) {
  .header {
    max-width: 1024px;
  }
}

@media screen and (width >= 1200px) {
  .header {
    max-width: 1200px;
  }
}

.header__logo {
  height: 40px;
  margin: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.nav__link:hover, .nav .is-active {
  color: #00acdc;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 48px 46px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 33px 17px 18px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
}

.card__title {
  color: #060b35;
  justify-content: center;
  width: 100%;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  align-self: flex-start;
  margin: 0 0 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid;
}

.card__price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  width: 100%;
  margin-bottom: 22px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__title {
  color: #060b35;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
